import { createRouter, createWebHistory } from "vue-router";
import Home from "../views/Home.vue";

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/about",
    name: "About",
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/About.vue"),
  },
  {
    path: "/shop",
    name: "Shop",
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/Shop.vue"),
  },
  {
    path: "/testimonials",
    name: "Testimonials",
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/Testimonials.vue"),
  },
  {
    path: "/book",
    name: "Book",
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/Book.vue"),
  },
  {
    path: "/thank-you",
    name: "ThankYou",
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/ThankYou.vue"),
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  scrollBehavior: function (to, _from, savedPosition) {
    if (savedPosition) {
      return savedPosition;
    }
    if (to.hash) {
      window.scrollTo(0, 2000);
    } else {
      window.scrollTo(0, 0);
    }
  },
});

export default router;
